import './App.css';
import './components/login.css';
import React from 'react';
import {Button} from 'reactstrap';
import {HashRouter as Router, Switch, Route, Link} from 'react-router-dom';
import FbLogin from './components/fbLogin';
import Login from './components/login';
import Logout from './components/logout';

class App extends React.Component {
  state = {
    fbButton: false,
    loggedIn: false,
    accessToken: '',
    loggedIn: false,
  }
  redirectPage = (value) => {
    this.setState({
      loggedIn: value
    })
  }
  showBtn = (value) => {
    this.setState({
      fbButton: value
    })
  }
  setAccess = (number) => {
    this.setState({
      accessToken: number
    })
  }
  render() {
    const {fbButton, loggedIn} = this.state;
    console.log('fbButton', fbButton)
      return (
        <Router basename='/'>
          <Switch>
                <Route exact path="/">
                  {fbButton === true &&
                    <div className="form">
                    <div className="rowMine">
                      <FbLogin loggedIn={loggedIn} redirectPage={this.redirectPage} setAccess={this.setAccess} fbButton={fbButton} showBtn={this.showBtn}  />
                    </div>
                    </div>
                  }
                  {fbButton === false &&
                    <div className="form">
                    <div className="rowMine">
                      <Login showBtn={this.showBtn} />
                    </div>
                    </div>
                  }
                </Route>       
                <Route path="/dashboard">
                  <React.Fragment>
                    <Logout redirectPage={this.redirectPage} />
                  </React.Fragment>
                </Route>
          </Switch>
        </Router>
      )
  }
}

export default App;
