import React from 'react';
import {Redirect} from 'react-router-dom';

class FbLogin extends React.Component {
  state = {}

  loadFbApi() {
    (function(d, s, id) {
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) return;
        js = d.createElement(s); js.id = id;
        js.src = "//connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
        console.log('fb sdk loaded')
    }(document, 'script', 'facebook-jssdk'));

    const statusChangeCallback = (response) => {
        console.log('statuschange', response)
        const {status} = response
        if (status === 'not_authorized' || status === 'unknown') {
            window.FB.login((response) => {
              let auth = response.authResponse;
              if (auth) {
                let accessToken = auth.accessToken;
                this.props.setAccess(accessToken);
                console.log(auth, accessToken)
                setTimeout(() => {this.props.redirectPage(true)}, 2000)
              }
            })
        } else {
            this.props.redirectPage(true)
        }}

    window.fbAsyncInit = function() {
        let FB_APP_ID = 1796391010517069;
        window.FB.init({
            appId      : FB_APP_ID,
            cookie     : true, 
            // the session
            xfbml      : true, 
            version    : 'v9.0' 
        })
        window.FB.AppEvents.logPageView();
        window.FB.getLoginStatus(function(response) {
          statusChangeCallback(response)
    });
    // window.fbAsyncInit()
    }}
    componentDidMount() {
        this.loadFbApi()
    }
    checkLoginState = () => {
        window.FB.getLoginStatus(function(response) {
        // statusChangeCallback(response);
        });
    }

    render() {
      const {loggedIn} = this.props;
      console.log('logged in', loggedIn)
        return (
            <React.Fragment>
              {loggedIn === true ? <Redirect to="/dashboard" /> : null}
                <div id="fb-root">
                  {/* <div onClick={this.handleClick}>FACEBOOK */}
                  <div 
                  data-onlogin={() => console.log('data login')}
                  className="fb-login-button" 
                  data-width="300" 
                  data-size="large" 
                  data-button-type="continue_with" 
                  data-layout="default" 
                  data-auto-logout-link="true" 
                  data-use-continue-as="false"
                  scope='read_insights,instagram_basic,pages_show_list,instagram_manage_comments,instagram_manage_insights,pages_read_engagement' 
                  >
                  </div>
                </div>
            </React.Fragment>
        )
    }
}

export default FbLogin;